import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setHideForm } from '../actions/ui';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import { startConfirm } from '../actions/family';


export const ConfirmScreen = () => {

    const dispatch = useDispatch();

    const { loading } = useSelector(state => state.ui);

    const { family } = useSelector(state => state.family);

    const { id, family_name, guests } = family;

    const handleBackClick = () => {
        const container = document.querySelector('.confirm-container');
        container.classList.remove('animate__backInRight');
        container.classList.add('animate__backOutRight');
        setTimeout(() => dispatch( setHideForm() ), 200);
    };

    const handleOnChange = guestId => e => {
        dispatch( startConfirm(id, guestId, e.target.checked) );
    };

    const confirmedGuests = guests.filter(g => g.goes);
    const unconfirmedGuests = guests.filter(g => !g.goes);

    const formatText = t => {
        if (t.length > 1) {
            return t.slice(0, t.length - 1).join(', ') + ' y ' + t[t.length - 1];
        }
        return t[0];
    };

    const textConfirmedGuests = formatText(confirmedGuests.map(c => c.nickname));
    
    const textUnconfirmedGuests = formatText(unconfirmedGuests.map(c => c.nickname));

    const allaNosVeremos = (<><br />{'¬°Nos vemos el 25 de Julio üòã!'}<br />{'Yo ver√©, la mejor pinta ese d√≠a üï∫üèªüíÉüèªü§≠'}</>);

    return (
        <aside className="confirm-container animate__animated animate__backInRight animate__faster">
            <div className="description">
                <h2>{ family_name }, ¬øNos acompa√±a{ guests.length > 1 ? 'n' : 's' }?</h2>
                <span>Para confirmar { guests.length > 1 ? 'la' : 'tu' } asistencia, solo debes decir { guests.length > 1 ? 'qui√©n va' : 'si vas' } o no a nuestra boda.</span>
                <span className="icon">ü•∞</span>
            </div>
            <hr />
            <form>
                {
                    guests.map(({ first_name, middle_name, last_name, second_last_name, goes, id }, i) => (
                        <div key={ i }>
                            <span className="name">{ `${`${ first_name } ${ middle_name }`.trim()} ${`${ last_name } ${ second_last_name }`.trim()}` }</span>
                            <FormControlLabel label={ goes ? 'üòä' : 'ü•∫' } title="Clic para cambiar" control={<Switch onChange={ handleOnChange(id) } checked={ goes } name="goes" color={ 'secondary' } />} disabled={ loading } />
                        </div>
                    ))
                }
            </form>
            <div className="message">
            {
                guests.length === confirmedGuests.length
                ?
                    (
                        <>
                            {`¬°Gracias ${ textConfirmedGuests } por acompa√±arnos ü§ó!`}
                            <br />
                            {
                                `Estamos ansiosos porque este d√≠a llegue ü•∞ y tener el placer de contar 
                                con ${ confirmedGuests.length === 1 ? 'tu' : 'su' } compa√±√≠a en este momento tan especial para nosotros üòÉ`
                            }
                            <br />
                            {allaNosVeremos}
                        </>
                    )
                :
                (
                    confirmedGuests.length === 0
                    ?
                    `Qu√© mal que no nos acompa√±a${ guests.length === 1 ? 's' : 'n' } üòï, pero sabemos que podemos contar con${ guests.length === 1 ? 'tigo' : ' ustedes' } para cualquier otra ocasi√≥n üí™üèª`
                    :
                    (
                        <>
                            {`¬°Gracias ${ textConfirmedGuests } por acompa√±arnos!`}
                            <br />
                            {allaNosVeremos}
                            <br />
                            <br />
                            {`Es una pena que ${ textUnconfirmedGuests } no nos acompa√±e${ unconfirmedGuests.length !== 1 ? 'n' : '' } ü•∫, pero sabemos 
                            que podemos contar con${ unconfirmedGuests.length === 1 ? 'tigo' : ' ustedes' } para cualquier otra ocasi√≥n üí™üèª`}
                        </>
                    )
                )
            }
            </div>
            <button onClick={ handleBackClick } disabled={ loading }>
                Regresar
            </button>
        </aside>
    )
}
